@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
  }

  body {
    background: url(/Anime-films/static/media/background.270a853e.jpg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: repeat;
    
  }
  
  .container {
    padding: 50px 0;
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .sorting {
    cursor: pointer;
    color: #fff;
    background: linear-gradient(135deg, rgba(80, 193, 245, 0.5) 0%, rgba(247, 192, 237, 0.5) 100%);
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-size: 18px;
    display: block;
    margin: 0 auto;
    width: 150px;
    height: 40px;
    border: none;
  }

  h1 {
    color: #fff;
    font-size: 40px;
    font-weight: 500;
  }

  /** scroll **/
  ::-webkit-scrollbar {
    width: 5px;
  }
  ::-webkit-scrollbar-track {
    background: #A0CCD7;
  }
  ::-webkit-scrollbar-thumb {
    background: #343F60;
  }
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }

.item {
    position: relative;
    color: #fff;
    margin: 10px;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 300px;
    height: 400px;
    background: linear-gradient(135deg, rgba(80, 193, 245, 0.5) 0%, rgba(247, 192, 237, 0.5) 100%);
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.item:nth-child(even):hover {
    -webkit-transform:skew(-3deg, 3deg);
            transform:skew(-3deg, 3deg);
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}

.item:nth-child(odd):hover {
    -webkit-transform:skew(3deg, -3deg);
            transform:skew(3deg, -3deg);
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

/* .item:hover {
   transform:skew(-3deg, 3deg);
   transition: transform .3s;
} */

.item-img {
    margin-top: 35px;
    width: 150px;
    height: 200px;
}

.item-img img {
    width: 100%;
    height: 100%;
}

.item-title {
    text-align: center;
    letter-spacing: .05em;
    margin-top: 15px;
    font-weight: 500;
    font-size: 25px;
}

.item-producer {
    margin-top: 5px;
    text-align: center;
    font-weight: 400;
    font-size: 19px;
}

.item-date {
    font-weight: 600;
    font-size: 19px;
}

.item-close {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #84C0F8;
    border: none;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    width: 50px;
    height: 50px;
    top: 0;
    left: 0;
    position: absolute;
    transition: background .2s;
}

.item-close:hover {
    background: #d7a0a0;
    transition: background .2s;
}

.item-like {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #2085ED;
    border: none;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    width: 50px;
    height: 50px;
    bottom: 0;
    right: 0;
    position: absolute;
}

.like-active {
    background: #8AF9A2;
}

.item-btn:focus {
    outline: none;
  }


.loading_wrapp {
    top: 0;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: #000000af;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: -1;
    opacity: 0;
  }
  
  .loading_wrapp-active {
    z-index: 1;
    opacity: 1;
    transition: all .3s;
  }
  
  .loading_wrapp h2 {
    font-size: 40px;
    letter-spacing: 0.05em;
    color: #a3a8be;
  }
